.modalHeader{
    font-style: normal;
    font-weight: 700;
    font-size: 24px;
    line-height: 28px;
    color: #000000;
    margin-left: 5px;
}

.addCoins{
    border: 1px solid #CFCFCF;
    box-sizing: border-box;
    border-radius: 6px;
    margin-top: 9px;
    text-align: center;
}

.addCoinsText{
    font-style: normal;
    font-weight: 500;
    font-size: 24px;
    line-height: 28px;
    letter-spacing: 0.0025em;
    color: #000000;
    margin: 12px 15px ;
}

.coinsList{
    display: flex;
    justify-content: space-around;
    margin-top: 34px;
}

.fiveHundred,.thousand,.fifteenHundred{
    background: #EBEFF0;
    border-radius: 16px;
    width: 76px;
    height: 34px;
    text-align: center;
} 

.fiveHundredText,.thousandText,.fifteenHundredText{
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 17px;
    color: #000000;
    margin-top: 7px;
}

.earnAndSave{
    background: #FFF5EF;
    border-radius: 16px;
    margin-top: 22px;
}

.saveHeader{
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    line-height: 19px;
    color: #223263;
    padding-top: 13px;
    margin-left: 18px;
    /* border: solid 1px; */
}

.saveContent{
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 14px;
    color: #726868;
    margin-top: 14px;
    margin-left: 38px;
}

.bottom {
    padding-bottom: 19px;
}
.referMore{
    font-style: normal;
    font-weight: 400;
    font-size: 10px;
    line-height: 14px;
    color: #223263;
    margin-top: 12px;
}

.redeemButtonPopUp{
    background: #FE8276;
    border: 1px solid #FE8276;
    box-sizing: border-box;
    box-shadow: 0px 4px 10px #A6CAD1;
    border-radius: 5px;
    /* height: 4.0625rem; */
    width: 100%;
    font-style: normal;
    font-weight: 500;
    font-size: 24px;
    height: 30px;
    line-height: 1.75rem;
    text-align: center;
    color: #FFFFFF;
    margin-top: 32px;
    margin-bottom: 50px;
}